#colorlib-aside {
    padding-top: 3em;
    padding-bottom: 40px;
    float: left;
    width: 300px;
    position: fixed;
    overflow-y: scroll;
    z-index: 1001;
    background: #e89a9a80;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s; }
    @media screen and (max-width: 768px) {
      #colorlib-aside {
        width: 300px;
        -moz-transform: translateX(-300px);
        -webkit-transform: translateX(-300px);
        -ms-transform: translateX(-300px);
        -o-transform: translateX(-300px);
        transform: translateX(-300px);
        padding-top: 4em; } }
    #colorlib-aside .author-img {
      width: 150px;
      height: 150px;
      margin: 0 auto;
      margin-bottom: 30px;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      border-radius: 50%; }
    #colorlib-aside .position {
      display: block;
      margin-bottom: 2em;
      font-weight:450;
      font-size: 15px;
      text-transform: capitalize; }
    #colorlib-aside #colorlib-logo {
      text-align: right;
      font-weight: 700;
      font-size: 22px;
      margin-bottom: .5em;
      display: block;
      width: 100%; }
      #colorlib-aside #colorlib-logo a {
        display: block;
        text-align: center;
        color: #000;
        padding-right: .3em; }
    #colorlib-aside #colorlib-main-menu {
      display: block;
      width: 100%; }
      #colorlib-aside #colorlib-main-menu .collapse {
        display: block !important; }
      #colorlib-aside #colorlib-main-menu ul {
        text-align: center;
        margin: 0;
        padding: 0; }
        @media screen and (max-width: 768px) {
          #colorlib-aside #colorlib-main-menu ul {
            margin: 0 0 2em 0; } }
        #colorlib-aside #colorlib-main-menu ul li {
          margin: 0 0 10px 0;
          padding: 0;
          list-style: none;
          font-size: 12px;
          font-weight: 500;
          text-transform: uppercase;
          letter-spacing: 1px; }
          #colorlib-aside #colorlib-main-menu ul li a {
            color: rgba(0, 0, 0, 0.7);
            text-decoration: none;
            position: relative;
            padding: 10px 0;
            font-family: "Quicksand", Arial, sans-serif;
            -webkit-transition: 0.3s;
            -o-transition: 0.3s;
            transition: 0.3s; }
            #colorlib-aside #colorlib-main-menu ul li a:after {
              content: "";
              position: absolute;
              height: 1px;
              bottom: 7px;
              left: 0;
              right: 0;
              background-color: #f715b1;
              visibility: hidden;
              -webkit-transform: scaleX(0);
              -moz-transform: scaleX(0);
              -ms-transform: scaleX(0);
              -o-transform: scaleX(0);
              transform: scaleX(0);
              -webkit-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
              -moz-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
              -ms-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
              -o-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
              transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275); }
            #colorlib-aside #colorlib-main-menu ul li a:hover {
              text-decoration: none;
              color: black; }
              #colorlib-aside #colorlib-main-menu ul li a:hover:after {
                visibility: visible;
                -webkit-transform: scaleX(1);
                -moz-transform: scaleX(1);
                -ms-transform: scaleX(1);
                -o-transform: scaleX(1);
                transform: scaleX(1); }
          #colorlib-aside #colorlib-main-menu ul li.active a {
            color: #f715b1; }
            #colorlib-aside #colorlib-main-menu ul li.active a:after {
              visibility: visible;
              -webkit-transform: scaleX(1);
              -moz-transform: scaleX(1);
              -ms-transform: scaleX(1);
              -o-transform: scaleX(1);
              transform: scaleX(1); }
    #colorlib-aside .colorlib-footer {
      font-size: 15px;
      text-align: center;
      font-weight: 400;
      color: rgba(0, 0, 0, 0.5);
      padding: 0 20px;
      width: 100%;
    }
    #colorlib-aside .colorlib-footer i{
        font-size: 20px;
    }
      @media screen and (max-width: 768px) {
        #colorlib-aside .colorlib-footer {
          position: relative;
          bottom: 0; } }
      #colorlib-aside .colorlib-footer span {
        display: block; }
      #colorlib-aside .colorlib-footer ul {
        padding: 0;
        margin: 0; }
        #colorlib-aside .colorlib-footer ul li {
          padding: 0;
          margin: 0;
          display: inline;
          list-style: none; }
          #colorlib-aside .colorlib-footer ul li a {
            color:rgba(0, 0, 0, 0.7);
            padding: 4px; 
        }
            #colorlib-aside .colorlib-footer ul li a:hover, #colorlib-aside .colorlib-footer ul li a:active, #colorlib-aside .colorlib-footer ul li a:focus {
              text-decoration: none;
              outline: none;
              color: #f715b1; }
  
body.offcanvas #colorlib-aside {
     -moz-transform: translateX(0);
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0);
    width: 300px;
    background: #e89a9a80;
    z-index: 999;
    position: fixed; 
}

.author-img {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative; 
}
  