.progress-wrap h3 {
    font-size: 16px;
    font-family: "Quicksand", Arial, sans-serif;
    margin-bottom: 10px; }
  

    .progress {
        height: 6px;
        -webkit-box-shadow: none;
        box-shadow: none;
        background: #fff;
        overflow: visible; }
      
      .progress-bar {
        background: #2c98f0;
        -webkit-box-shadow: none;
        box-shadow: none;
        font-size: 12px;
        line-height: 1.2;
        color: #000;
        font-weight: 600;
        text-align: right;
        position: relative;
        overflow: visible;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        -ms-border-radius: 8px;
        border-radius: 8px; }
        .progress-bar:after {
          position: absolute;
          top: -2px;
          right: 0;
          width: 10px;
          height: 10px;
          content: '';
          background: #2c98f0;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          border-radius: 50%; }
        .progress-bar span {
          position: absolute;
          top: -22px;
          right: 0; }
        .progress-bar.color-1 {
          background: #2c98f0; }
          .progress-bar.color-1:after {
            background: #2c98f0; }
          .progress-bar.color-1 span {
            color: #2c98f0; }
        .progress-bar.color-2 {
          background: #ec5453; }
          .progress-bar.color-2:after {
            background: #ec5453; }
          .progress-bar.color-2 span {
            color: #ec5453; }
        .progress-bar.color-3 {
          background: #f9bf3f; }
          .progress-bar.color-3:after {
            background: #f9bf3f; }
          .progress-bar.color-3 span {
            color: #f9bf3f; }
        .progress-bar.color-4 {
          background: #a84cb8; }
          .progress-bar.color-4:after {
            background: #a84cb8; }
          .progress-bar.color-4 span {
            color: #a84cb8; }
        .progress-bar.color-5 {
          background: #2fa499; }
          .progress-bar.color-5:after {
            background: #2fa499; }
          .progress-bar.color-5 span {
            color: #2fa499; }
        .progress-bar.color-6 {
          background: #4054b2; }
          .progress-bar.color-6:after {
            background: #4054b2; }
          .progress-bar.color-6 span {
            color: #4054b2; }
