#cd-timeline {
  position: relative;
  padding: 2em 0;
  margin-bottom: 2em;
}
#cd-timeline::before {
  content: '';
  position: absolute;
  top: 0;
  left: 25px;
  height: 100%;
  width: 4px;
  background: #f715b1;
}
@media only screen and (min-width: 1170px) {
  #cd-timeline {
    margin-bottom: 3em;
 }
  #cd-timeline::before {
    left: 50%;
    margin-left: -2px;
 }
}
.cd-timeline-block {
  position: relative;
  margin: 2em 0;
}
.cd-timeline-block:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-block:first-child {
  margin-top: 0;
}
.cd-timeline-block:last-child {
  margin-bottom: 0;
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-block {
    margin: 4em 0;
 }
  .cd-timeline-block:first-child {
    margin-top: 0;
 }
  .cd-timeline-block:last-child {
    margin-bottom: 0;
 }
}
.cd-timeline-img {
  position: absolute;
  top: 8px;
  left: 12px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  box-shadow: 0 0 0 4px #f715b1, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
}
.cd-timeline-img {
  background: #e89a9a;
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-img {
    width: 30px;
    height: 30px;
    left: 50%;
    margin-left: -15px;
    margin-top: 15px;
   /* Force Hardware Acceleration in WebKit */
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
 }
}
.cd-timeline-content {
  position: relative;
  margin-left: 60px;
  margin-right: 30px;
  background: #e89a9a;
  border-radius: 2px;
  padding: 1em;
}
.cd-timeline-content .timeline-content-info {
  background: #f2dfdf;
  padding: 5px 10px;
  color: rgb(0,0,0);
  font-size: 13px;
  font-weight: 600;
  box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.08);
  border-radius: 2px;
}
.cd-timeline-content .timeline-content-info i {
  margin-right: 5px;
}
.cd-timeline-content .timeline-content-info .timeline-content-info-title, .cd-timeline-content .timeline-content-info .timeline-content-info-date {
  width: calc(100% - 2px);
  display: inline-block;
}

@media (max-width: 500px) {
  .cd-timeline-content .timeline-content-info .timeline-content-info-title, .cd-timeline-content .timeline-content-info .timeline-content-info-date {
    display: block;
    width: 100%;
 }
}
.cd-timeline-content .content-skills {
  font-size: 12px;
  padding: 0;
  margin-bottom: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.cd-timeline-content .content-skills li {
  background: #f2dfdf;
  border-radius: 2px;
  display: inline-block;
  padding: 2px 10px;
  color: rgba(0,0,0);
  font-weight: 600;
  margin: 3px 2px;
  text-align: center;
  flex-grow: 1;
}
.cd-timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-content h2 {
  color: rgb(0,0,0);
  margin-top: 0;
  margin-bottom: 5px;
 
}
.cd-timeline-content p, .cd-timeline-content .cd-date {
  color: rgba(0,0,0);
  font-size: 15px;
  /*font-size: 0.8125rem;*/
}
.cd-timeline-content .cd-date {
  display: inline-block;
}
.cd-timeline-content p {
  margin: 1em 0;
  line-height: 1.6;
}
.cd-timeline-content::before {
  content: '';
  position: absolute;
  top: 16px;
  right: 100%;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-right: 7px solid #e89a9a;
}
@media only screen and (min-width: 768px) {
  .cd-timeline-content h2 {
    font-size: 20px;
   /* font-size: 1.25rem;*/
   text-decoration: center;
   text-align: center;
 }
  .cd-timeline-content p {
    font-size: 16px;
    font-weight: 500;
  /*  font-size: 1rem;*/
 }
  .cd-timeline-content .cd-read-more, .cd-timeline-content .cd-date {
    font-size: 14px;
    /*font-size: 0.875rem;*/
 }
 .cd-timeline-content a{
  font-weight: 600;
  color:#ad2a2a
 }
}
@media(max-width: 767px) {
  .cd-timeline-content h2 {
    font-size: 18px;
  }
}

@media only screen and (min-width: 1170px) {
  .cd-timeline-content {
    color: white;
    margin-left: 0;
    padding: 1.6em;
    width: 36%;
    margin: 0 5%;
 }
  .cd-timeline-content::before {
    top: 24px;
    left: 100%;
    border-color: transparent;
    border-left-color: #e89a9a;
 } 
  .cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    left: 140%;
    top: 6px;
    font-size:20px;
    font-weight: 600;
    
 }
  .cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
 }
  .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 24px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color:  #e89a9a;
 }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
 }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    right:140%;
    text-align: right;
 }
}

